import React, {useContext} from 'react';
import getString from '../../../../langs/getString';
import LanguageContext from '../../../../contexts/LanguageContext';
import ScreenshotsListMobile from '../ScreenshotsList/ScreenshotsListMobile';

import styles from './ScreenshotsSection.module.scss';

import screenshot1 from '../../../../assets/screenshot-1.png';
import screenshot2 from '../../../../assets/screenshot-2.png';
import screenshot3 from '../../../../assets/screenshot-3.png';
import screenshot4 from '../../../../assets/screenshot-4.png';
import screenshot5 from '../../../../assets/screenshot-5.png';
import screenshot6 from '../../../../assets/screenshot-6.png';

const ScreenshotsSection: React.FC = () => {
  const [language] = useContext(LanguageContext);

  const screenshots = language === 'ru'
    ? [screenshot1, screenshot2, screenshot3]
    : [screenshot4, screenshot5, screenshot6];

  return (
    <section
      id="screenshots_section"
      className={styles.screenshots_section}
    >
      <h4 className={styles.screenshots_section__header}>
        {getString(language, 'user_friendly_interface')}
      </h4>

      <div className={styles.screenshots_section__carousel_container}>
        <div className="d-lg-none">
          <ScreenshotsListMobile screenshots={screenshots}/>
        </div>

        <div className="d-none d-lg-flex justify-content-around">
          {screenshots.map((src, index) => (
            <img
              key={src}
              src={src}
              alt={`Screenshot ${index + 1}`}
              className={styles.screenshots_section__screenshot}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default ScreenshotsSection;
