import React, {CSSProperties, useRef} from 'react';
import Carousel, {CarouselRef} from 'react-bootstrap/Carousel';

import styles from './ImageSlider.module.scss';

import arrowRight from '../../../assets/arrow-right.svg';

interface ImageSliderProps {
  images: string[];
  className?: string;
  style?: CSSProperties;
}

const ImageSlider: React.FC<ImageSliderProps> = ({
                                                   images,
                                                   style,
                                                   className,
                                                 }) => {
  const carouselRef = useRef<CarouselRef>(null);

  const onPrevClick = () => carouselRef?.current?.prev?.();
  const onNextClick = () => carouselRef?.current?.next?.();

  return (
    <aside className={`${styles.image_slider} ${className}`} style={style}>
      <img
        className={styles.image_slider__control}
        style={{ transform: 'rotate(180deg)' }}
        src={arrowRight}
        alt="Previous"
        onClick={onPrevClick}
      />

      <Carousel
        ref={carouselRef}
        className={styles.image_slider__carousel}
        controls={false}
      >
        {images.map((src, index) => (
          <Carousel.Item key={src}>
            <img
              className={styles.image_slider__image}
              src={src}
              alt={`Slide ${index + 1}`}
            />
          </Carousel.Item>
        ))}
      </Carousel>

      <img
        className={styles.image_slider__control}
        src={arrowRight}
        alt="Next"
        onClick={onNextClick}
      />
    </aside>
  );
};

ImageSlider.defaultProps = {
  className: '',
  style: {},
};

export default ImageSlider;
