import React, {useContext} from 'react';
import Language from '../../../../types/Language';
import LanguageContext from '../../../../contexts/LanguageContext';

import russianFlag from '../../../../assets/russian-flag.svg';
import usaFlag from '../../../../assets/usa-flag.svg';

import styles from './LanguageSwitcher.module.scss';

interface LanguageIcon {
  icon: string;
  alt: string;
  value: Language;
}

const LanguageSwitcher: React.FC = () => {
  const [language, setLanguage] = useContext(LanguageContext);

  const languages: Array<LanguageIcon> = [
    { icon: russianFlag, alt: 'Русский', value: 'ru' },
    { icon: usaFlag, alt: 'English', value: 'en' },
  ];

  return <div className={styles.language_switcher}>{
    languages.map(({ value, alt, icon }) => (
      <img
        key={value}
        className={
          `
          ${styles.language_switcher__icon} 
          ${value === language ? styles.language_switcher__icon__active : ''}
          `
        }
        onClick={() => setLanguage(value)}
        src={icon}
        alt={alt}
      />
    ))}
  </div>;
};

export default LanguageSwitcher;
