import React, {useContext} from 'react';
import getString from '../../../../langs/getString';
import LanguageContext from '../../../../contexts/LanguageContext';
import Navbar from '../../../../components/smart/Navbar/Navbar';
import Button from '../../../../components/dumb/Button/Button';

import './IntroSection.scss';
import styles from './IntroSection.module.scss';

import logo from '../../../../assets/logo.png';

const IntroSection: React.FC = () => {
  const [language] = useContext(LanguageContext);

  return (
    <div className={styles.intro}>
      <Navbar/>

      <article className={styles.intro__article}>
        <h1 className={styles.intro__header}>
          {getString(language, 'app_name')}
        </h1>

        <p className={styles.intro__paragraph}>
          <span style={{ fontWeight: 'bold' }}>
            «{getString(language, 'app_name')}»
          </span>&nbsp;-&nbsp;

          {getString(language, 'description').split('<br/>').map(
            (str) => <React.Fragment key={str}>{str}<br/></React.Fragment>,
          )}
        </p>

        <a href="#download_section">
          <Button className={styles.intro__button}>
            {getString(language, 'download')}
          </Button>
        </a>
        <a href="#screenshots_section" className={styles.intro__link}>
          {getString(language, 'watch_screenshots')}
        </a>

        <img id="lollipop_1" src={logo} alt="Lollipop"/>
        <img id="lollipop_2" src={logo} alt="Lollipop"/>
      </article>
    </div>
  );
};

export default IntroSection;
